  import React, { useState, useMemo, useCallback } from 'react';
  import { useSelector } from 'react-redux';
  import PortfolioList from '../components/portfolio/PortfolioList';
  import ScrollButtons from '../components/ui/ScrollButtons';
  import './Work.css';

  /**
   * Work  -  // ( )
   *  React.memo + useCallback  
   *    +  
   *    +  
   */
  const Work = React.memo(() => {
    //  Redux   
    const portfolios = useSelector((state) => state?.portfolio?.portfolios || []);
    const categories = useSelector((state) => state?.portfolio?.categories || []);

    //   - useCallback 
    const [selectedCategory, setSelectedCategory] = useState('all');
    const [searchQuery, setSearchQuery] = useState('');
    const [sortBy, setSortBy] = useState('date-desc');

    //  // -   
    const filteredAndSortedData = useMemo(() => {
      //   
      if (!Array.isArray(portfolios) || portfolios.length === 0) {
        return [];
      }

      let result = [...portfolios];

      // 1.   (  )
      if (selectedCategory !== 'all') {
        result = result.filter((item) => 
          item?.category === selectedCategory
        );
      }

      // 2.   (  )
      if (searchQuery.trim()) {
        const q = searchQuery.trim().toLowerCase();
        result = result.filter((item) => {
          const title = String(item?.title || '').toLowerCase();
          const desc = String(item?.description || '').toLowerCase();
          const tags = Array.isArray(item?.tags) 
            ? item.tags.map(String).map(tag => tag.toLowerCase())
            : [];
          
          return (
            title.includes(q) ||
            desc.includes(q) ||
            tags.some((tag) => tag.includes(q))
          );
        });
      }

      // 3.  ( / )
      switch (sortBy) {
        case 'date-desc':
          result.sort((a, b) => {
            const dateA = new Date(a?.date || 0);
            const dateB = new Date(b?.date || 0);
            return dateB - dateA;
          });
          break;
        case 'date-asc':
          result.sort((a, b) => {
            const dateA = new Date(a?.date || 0);
            const dateB = new Date(b?.date || 0);
            return dateA - dateB;
          });
          break;
        case 'title-asc':
          result.sort((a, b) => 
            String(a?.title || '').localeCompare(String(b?.title || ''), 'ko')
          );
          break;
        case 'title-desc':
          result.sort((a, b) => 
            String(b?.title || '').localeCompare(String(a?.title || ''), 'ko')
          );
          break;
        default:
          break;
      }

      return result;
    }, [portfolios, selectedCategory, searchQuery, sortBy]);

    //   
    const handleResetFilters = useCallback(() => {
      setSelectedCategory('all');
      setSearchQuery('');
      setSortBy('date-desc');
    }, []);

    //    
    if (!Array.isArray(portfolios) || portfolios.length === 0) {
      return (
        <div className="work-page" role="main" aria-label="  ">
          <div className="work-header">
            <h1 className="work-title">Portfolio</h1>
            <div className="work-subtitle">
              <div className="loading-spinner"></div>
                 ...
            </div>
          </div>
        </div>
      );
    }

    return (
      <div className="work-page" role="main" aria-label=" ">
        {/*  */}
        <div className="work-header">
          <h1 className="work-title">Portfolio</h1>
          <p className="work-subtitle">
             <strong>{portfolios.length}</strong> {' '}
            <strong>{filteredAndSortedData.length}</strong> 
          </p>
        </div>

        {/*   */}
        <div className="work-controls" role="region" aria-label="  ">
          {/*   */}
          <div className="category-filter" role="tablist">
            <button
              type="button"
              className={`category-btn ${selectedCategory === 'all' ? 'active' : ''}`}
              onClick={() => setSelectedCategory('all')}
              aria-selected={selectedCategory === 'all'}
              aria-controls="portfolio-list"
              role="tab"
            >
              
            </button>
            {categories.map((cat) => (
              <button
                key={cat.id}
                type="button"
                className={`category-btn ${selectedCategory === cat.id ? 'active' : ''}`}
                style={{ '--category-color': cat.color }}
                onClick={() => setSelectedCategory(cat.id)}
                aria-selected={selectedCategory === cat.id}
                aria-controls="portfolio-list"
                role="tab"
              >
                {cat.label}
              </button>
            ))}
          </div>

          {/*  +  */}
          <div className="search-sort-wrapper">
            <div className="search-box" role="search">
              <input
                className="search-input"
                type="search"
                placeholder=", ,  ..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                aria-label=" "
              />
              {searchQuery && (
                <button
                  type="button"
                  className="search-clear-btn"
                  onClick={() => setSearchQuery('')}
                  aria-label=" "
                >
                  
                </button>
              )}
            </div>

            <select
              className="sort-select"
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value)}
              aria-label="  "
            >
              <option value="date-desc"></option>
              <option value="date-asc"> </option>
              <option value="title-asc"> </option>
              <option value="title-desc"> </option>
            </select>
          </div>
        </div>

        {/*   */}
        <div id="portfolio-list" role="list">
          {filteredAndSortedData.length > 0 ? (
            <PortfolioList portfolios={filteredAndSortedData} />
          ) : (
            <div className="no-results" role="status" aria-live="polite">
              <div className="no-results-content">
                <div className="no-results-icon"></div>
                <p>  .</p>
                <p className="no-results-hint">
                      .
                </p>
                <button
                  type="button"
                  className="reset-btn"
                  onClick={handleResetFilters}
                  aria-label="  "
                >
                   
                </button>
              </div>
            </div>
          )}
        </div>

        {/*   */}
        <ScrollButtons />
      </div>
    );
  });

  Work.displayName = 'WorkPage';

  export default Work;
