    import React, { useCallback, useMemo } from 'react';
    import { Link } from 'react-router-dom';
    import { getImageUrl } from '../../utils/imageUtils';
    import './PortfolioCard.css';

    /**
     * PortfolioCard -   (  )
     *     (imagePath, item  portfolio)
     *  React.memo + useCallback  
     *      +  
     *  SEO + LCP 
     */
    const PortfolioCard = React.memo(({ portfolio, onClick }) => {
    //     + 
    const {
        id = '',
        title = 'Untitled Project',
        category = 'Uncategorized',
        thumbnail = '',
        tags = [],
        description = ''
    } = portfolio || {};

    //     
    const imagePath = useMemo(() => {
        if (thumbnail?.trim()) return thumbnail;
        return ''; // CSS  
    }, [thumbnail]);

    const imageSrc = useMemo(() => 
        imagePath ? getImageUrl(imagePath) : '', 
    [imagePath]);

    //     -    
    const handleImageError = useCallback((e) => {
        const img = e.target;
        // SVG   
        img.src = 'data:image/svg+xml;base64,';
        
        //     +  
        img.onerror = null;
        img.dataset.failed = 'true';
        img.style.opacity = '0';
    }, []);

    //    
    const handleImageLoad = useCallback((e) => {
        e.target.onerror = null;
        e.target.dataset.loaded = 'true';
    }, []);

    //   ( 2)
    const visibleTags = useMemo(() => 
        Array.isArray(tags) ? tags.slice(0, 2) : [], 
    [tags]);

    return (
        <Link 
        to={`/portfolio/${id}`}
        className="portfolio-card"
        title={`${title} - ${category}  `}
        aria-label={`${title}, ${category} ,    `}
        onClick={onClick}
        style={{ textDecoration: 'none' }}
        >
        {/*   */}
        <div className="portfolio-card__image-wrapper" role="img" aria-label={`${title}  `}>
            <img 
            src={imageSrc}
            alt={`${title} - ${category}   `}
            className="portfolio-card__image"
            loading="lazy"
            width="400"
            height="250"
            decoding="async"
            onError={handleImageError}
            onLoad={handleImageLoad}
            draggable={false}
            />
            
            {/*   */}
            <div className="portfolio-card__overlay">
            <span className="portfolio-card__view" aria-hidden="true">
                View Project 
            </span>
            </div>
        </div>

        {/*   */}
        <div className="portfolio-card__content">
            {/*  */}
            {visibleTags.length > 0 && (
            <div className="portfolio-card__categories" aria-label={`${visibleTags.length} `}>
                {visibleTags.map((tag, index) => (
                <span 
                    key={`${id}-tag-${index}`} 
                    className="portfolio-card__tag"
                >
                    {String(tag)}
                </span>
                ))}
                {tags.length > 2 && (
                <span className="portfolio-card__tag more">+{tags.length - 2}</span>
                )}
            </div>
            )}

            {/*  */}
            <h3 className="portfolio-card__title">{title}</h3>
            
            {/*  */}
            <span className="portfolio-card__category" aria-label={`: ${category}`}>
            {category}
            </span>
        </div>
        </Link>
    );
    });

    PortfolioCard.displayName = 'PortfolioCard';

    export default PortfolioCard;
