import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';

/**
 * ScrollToTop 
 *        .
 * React Router useLocation    .
 */
    function ScrollToTop() {
    //    
    const { pathname } = useLocation();

    useEffect(() => {
        //      
        window.scrollTo(0, 0);
    }, [pathname]); // pathname  :    

    // UI   
    return null;
    }

export default ScrollToTop;
