    import { createSlice } from "@reduxjs/toolkit"
    import portfolioData from "@data/portfolioData.json" //  JSON import

    //    - JSON  
    const initialState = {
    portfolios: portfolioData.portfolios,  // 
    categories: portfolioData.categories,  // 
    filteredPortfolios: portfolioData.portfolios,
    selectedCategory: "all",
    searchTerm: "",
    sortBy: "recent",
    }

    const portfolioSlice = createSlice({
    name: "portfolio",
    initialState,
    reducers: {
        setCategory: (state, action) => {
        state.selectedCategory = action.payload
        //  ...
        },
        setSearchTerm: (state, action) => {
        state.searchTerm = action.payload
        //  ...
        },
        setSortBy: (state, action) => {
        state.sortBy = action.payload
        //  ...
        },
    },
    })

    export const { setCategory, setSearchTerm, setSortBy } = portfolioSlice.actions
    export default portfolioSlice.reducer
